import "./styles.css";
import { useState } from "react";
import { Form } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
export default function App() {
  const [input, setInput] = useState("");
  let result = "";
  const convert = (num) => {
    const obj = {
      M: 1000,
      CM: 900,
      D: 500,
      CD: 400,
      C: 100,
      XC: 90,
      L: 50,
      XL: 40,
      X: 10,
      IX: 9,
      V: 5,
      IV: 4,
      I: 1
    };

    for (let key in obj) {
      if (typeof num === "number") {
        const repeated = Math.floor(num / obj[key]);
        if (repeated !== 0) {
          result += key.repeat(repeated);
        }
        num %= obj[key];
        if (num === 0) return result;
      } else {
        console.log("Nan");
      }
    }
    return result;
  };
  const submitF = (e) => {
    e.preventDefault();
    convert(+input);
  };

  const inputH = (e) => {
    setInput(e.target.value);
  };
  return (
    <div className="main">
      <h3 style={{ margin: "12px" }} className="text-center">
        Type a number
      </h3>
      <form
        style={{
          display: "flex",
          margin: "50px auto",
          width: "39%",
          justifyContent: "space-between"
        }}
      >
        <input
          value={input}
          onChange={inputH}
          style={{
            width: "70%",
            border: "1px solid #000050",
            borderRadius: "7px"
          }}
          placeholder="Type a number"
          type="number"
        />
        <button onClick={submitF} type="submit" className="btn btn-secondary">
          Convert
        </button>
      </form>
      <div
        className="text-center"
        style={{ color: "black", fontSize: "1.2rem" }}
      >
        Result is {convert(+input)}
      </div>
    </div>
  );
}
